import React, { useState, useEffect } from 'react';
import { Navbar, Container, Nav, NavDropdown, Image } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import '../styles/header.css';

// Ensure motion framer components have initial values
const defaultMotionProps = {
  initial: { opacity: 0 },
  animate: { opacity: 1 },
  transition: { duration: 0.3 }
};

const Header = () => {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const navVariants = {
    hidden: { opacity: 0, y: -20 },
    visible: { 
      opacity: 1, 
      y: 0,
      transition: { 
        staggerChildren: 0.1,
        duration: 0.5 
      } 
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: -10 },
    visible: { opacity: 1, y: 0 }
  };

  return (
    <header className={`site-header ${scrolled ? 'scrolled' : ''}`}>
      <Navbar 
        expand="lg" 
        fixed="top" 
        className={scrolled ? 'scrolled' : ''}
      >
        <Container>
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5 }}
          >
            <Navbar.Brand as={Link} to="/" className="logo-container">
              <Image 
                src="/images/logo.png" 
                alt="Logo" 
                className="logo"
                onError={(e) => {
                  e.target.src = 'https://via.placeholder.com/150x50?text=LOGO';
                }}
              />
            </Navbar.Brand>
          </motion.div>

          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          
          <Navbar.Collapse id="basic-navbar-nav">
            <motion.div
              className="mx-auto nav-links-container"
              initial="hidden"
              animate="visible"
              variants={navVariants}
            >
              <Nav className="align-items-center">
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/" className="nav-link-highlight">Home</Nav.Link>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/about" className="nav-link-highlight">About</Nav.Link>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <NavDropdown 
                    title="Services" 
                    id="services-dropdown"
                    className="custom-dropdown"
                  >
                    <NavDropdown.Item href="https://searchmycolleges.com/" target="_blank" rel="noopener noreferrer">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">🎓</span>
                        <div>
                          <span className="dropdown-item-title">SMC</span>
                          <p className="dropdown-item-desc">Search My Colleges</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                    <NavDropdown.Item href="https://searchmytrips.com/" target="_blank" rel="noopener noreferrer">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">✈️</span>
                        <div>
                          <span className="dropdown-item-title">SMT</span>
                          <p className="dropdown-item-desc">Search My Trips</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                    <NavDropdown.Item href="https://searchmymedical.com/" target="_blank" rel="noopener noreferrer">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">⚕️</span>
                        <div>
                          <span className="dropdown-item-title">SMM</span>
                          <p className="dropdown-item-desc">Search My Medical</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                  </NavDropdown>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/next" className="nav-link-highlight">NExT</Nav.Link>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <NavDropdown 
                    title="Quizzes" 
                    id="quizzes-dropdown"
                    className="custom-dropdown"
                  >
                    <NavDropdown.Item as={Link} to="/mock-tests">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">📝</span>
                        <div>
                          <span className="dropdown-item-title">Mock Tests</span>
                          <p className="dropdown-item-desc">Practice exams</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                    <NavDropdown.Item as={Link} to="/courses">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">📚</span>
                        <div>
                          <span className="dropdown-item-title">Courses</span>
                          <p className="dropdown-item-desc">Educational courses</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                    <NavDropdown.Item as={Link} to="/study-materials">
                      <div className="dropdown-item-content">
                        <span className="dropdown-icon">📖</span>
                        <div>
                          <span className="dropdown-item-title">Study Materials</span>
                          <p className="dropdown-item-desc">Learning resources</p>
                        </div>
                      </div>
                    </NavDropdown.Item>
                  </NavDropdown>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/blogs" className="nav-link-highlight">Blogs</Nav.Link>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/articles" className="nav-link-highlight">Articles</Nav.Link>
                </motion.div>
                
                <motion.div variants={itemVariants}>
                  <Nav.Link as={Link} to="/contact" className="nav-link-highlight">Contact</Nav.Link>
                </motion.div>
              </Nav>
            </motion.div>
            
            <motion.div 
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.5, duration: 0.5 }}
              className="ms-auto"
            >
              <Link to="/login" className="btn btn-primary login-btn">
                Login
              </Link>
            </motion.div>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};

export default Header; 